<template>
  <div>
    <LivePlayer
      @snapOutside="snapOutside"
      ref="player"
      :videoUrl="src"
      live
      digitalZoom
      :controls="false"
      @play="handlePlay"
    />
  </div>
</template>

<script>
import LivePlayer from "@liveqing/liveplayer";

export default {
  components: {
    LivePlayer,
  },
  props: {
    msg: String,
    src: String,
  },
  methods: {
    pause: function () {
      this.$refs.player.pause();
    },
    play: function () {
      this.$refs.player.play();
    },
    snap: function () {
      this.$refs.player.snap();
    },
    snapOutside: function (snapData) {
      alert(snapData);
    },
    handlePlay() {
      const videoElement = this.$refs.player.$el.querySelector("video");
      if (videoElement && videoElement.videoWidth > 0) {
        this.$emit("resolution-ready", {
          videoWidth: videoElement.videoWidth,
          boxWdith: videoElement.offsetWidth,
        });
        console.log(
          `原始分辨率: ${videoElement.videoWidth}x${videoElement.videoHeight}`
        );
      }
    },
  },
  beforeDestroy() {
    this.$emit("closeVideo");
  },
};
</script>

<style lang="scss" scoped></style>
